.action_btn {
  position: fixed;
  bottom: 0px;
  right: 0px;
  width: 100vw;
  height: 50px;
  background-color: #414547;
  display: flex;
  text-align: center;
  line-height: 50px;
  border-top: 0.1px solid rgba(250, 253, 254, 0.3);
  z-index: 999;
}

@media screen and (min-width: 1000px) {
  .action_btn {
    display: none;
  }
}

.action_btn__left, .action_btn__right {
  transition: 0.3s;
  width: 50vw;
}

.action_btn__left a, .action_btn__right a {
  position: relative;
  padding-left: 30px;
}

.action_btn__left a::before, .action_btn__right a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 7px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  background-size: contain;
  background-repeat: no-repeat;
}

@media screen and (max-width: 414px) {
  .action_btn__left a::before, .action_btn__right a::before {
    left: 3px;
  }
}

.action_btn__left:active, .action_btn__right:active {
  background-color: #fff;
  color: #414547;
  transition: 0.3s;
}

.action_btn__left {
  border-right: 0.1px solid rgba(250, 253, 254, 0.3);
}

.action_btn__left a::before {
  width: 15px;
  height: 15px;
  background-image: url(images/tel.png);
  transition: 0.3s;
}

.action_btn__left a:active::before {
  background-image: url(images/tel_b.png);
  transition: 0.3s;
}

.action_btn__right a::before {
  height: 20px;
  width: 20px;
  background-image: url(images/phone.png);
}

